a {
    text-decoration: none;
    color: #2D2641;
    font-family: 'Karla', sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 1.5px;
    transition: 0.25s;
}

a:hover {
    color: #2D2641;
    text-decoration: underline;
}